/*Slides*/
.slides {
  width: 100%;
  height: 100%;
}
.slide {
  background: url("../img/bg1.png") no-repeat;
  background-size: 100%100%;
    height: 500px;
		& div {
    text-align: center;
  	color: #fff;
    position: absolute;
    top: 50%;
    left:10%;
    font-size:1.5em;
    & h2{
      font-weight: 100;
    }
    p:nth-child(1){
      border-top: 1px solid orange;
      width: 60px;
      position: relative;
      bottom: 15px;
      right: 15px;
    }
    p:nth-child(3){
      border-bottom: 1px solid orange;
      width: 60px;
      position: relative;
      bottom: -15px;
      left: 140px;
    }
	}
}

.slick-slider{
  margin:0;
}
.slick-dots{
  all: initial;
  position: relative;
  bottom: 50px;
  margin-left: 50px;
}
.slick-dots li.slick-active button:before{
  color:$white;
}
.slick-dots li button:before{
	font-size: 12px;
  color:$white;
}
.slick-dots li{
	margin:0;
}

.slick-list{
  height: 500px;
}

@include tablet{ 
  .slick-list{
    height: 600px;
  }
  .slide{
    height: 600px;
  }
}